.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-direction: column; /* stack everything */
}

h1 {
  margin-block-end: 0;
}

p {
  font-size: 92%;
  margin-block-start: 0;
}

/* .App-logo {
  height: 40vmin;
  pointer-events: none;
} */

/* @media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
} */

.App-header {
  font-size: calc(10px + 2vmin);
  color: #000000;
}

.surfspots {
  column-gap: 40px;
  margin: auto;
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-flow: row wrap;
  color: #3bbae8;
}

.noDisplay {
  display: none;
}

.riversurf {
  flex: 1;
}

.tableHeader{
  display: flex;
}

.tableHeaderCol{
  flex: 1;
}

.bungeesurf {
  flex: 1;
}

.spotsContainer {
  width: 700px;
  margin: auto;
}

.spot {
  display: flex;
  padding: 20px 10px;
  margin: 20px 10px;
  color: #000000;
  background-color: #f5f5f5;
  border-radius: 20px;
}

.miniForecast {
  flex: 1;
}

details>summary span.collapsibleIcon {
  transition: all 1s;
  align-self: center;
}

details[open] summary span.collapsibleIcon {
  transform: rotate(180deg);
}

.collapsibleIcon {
  flex: 0.1;
}

.collapsibleIcon>img {
  width: 100%;
  cursor: pointer;
}

summary::-webkit-details-marker {
  display: none;
}

.collapsibleContent {
  margin-top: 20px;
  margin-left: -30px;
}

.spotname {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 130%;
  font-weight: bold;
  /* background-color: blueviolet; */
}

.spotname a {
  flex: 1;
  text-decoration: none;
  color: #000000;
  overflow: hidden;
  text-overflow: ellipsis;
}

.meas {
  padding: 0 20px;
  text-align: center;
  font-weight: bold;
  display: flex;
  align-content: center;
}

.unit {
  display: inline;
}

.smallFont {
  font-size: 70%;
}

.flow {
  flex: 1;
  /* background-color: cadetblue; */
  align-items: flex-end;
  font-size: 120%;
}

.measurements {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex: 1;
  row-gap: 5px;
}

.doubleCol {
  flex: 2;
}

.stars {
  flex: 1;
  align-items: center;
  display: flex;
  justify-content: center;
}

.forecast {
  font-size: 20px;
  font-weight: normal;
  flex: 1;
  align-items: center;
}

.forecastContainer {
  margin: 0 auto;
  display: flex;
  width: 160px;
  column-gap: 5px;
}

.invisible {
  visibility: hidden;
}

.temp {
  flex: 1;

  font-size: 90%;
  /* background-color: coral; */
}

/* colors for flow */
.flow_bad {
  color: #e2576f;
}
.flow_ok {
  color: #f8cd4c;
}
.flow_good {
  color: #409073;
}
.flow_danger {
  color: #e2576f;
}

/* colors for temperature */
.temp_0 {
  color: #5944fa;
}
.temp_1 {
  color: #3b91f4;
}
.temp_2 {
  color: #7cece5;
}
.temp_3 {
  color: #f6db2a;
}
.temp_4 {
  color: #ec5529;
}

/* .riversurf {
  background-color: aquamarine;
} */

/* .Link {
  color: rgb(0, 184, 230);
} */

ul {
  list-style: none;
  padding-left: 0;
}

.Footer {
  padding: 1.25rem 1.875rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: white;
  background-color: #3bbae8;
}

/* stack footer items vertically on small touch screens */
@media only screen and (max-width: 1000px) {
  .surfspots {
    flex-direction: column;
    width: 100%;
  }

  .Footer {
    flex-direction: column;
  }

  .collapsibleIcon {
    width: 15px;
  }
}

@media only screen and (max-width: 720px) {
  .tableHeader {
    display: none;
  }

  .spotsContainer {
    width: auto;
  }

  .spotname {
    flex-direction: column;
    row-gap: 20px;
  }

  .invisible {
    display: none;
  }

  .forecastContainer {
    flex-direction: column;
  }
}

.Footer a {
  color: white;
}

.Footer_item {
  margin: auto;
  padding: 1rem;
  min-width: 12.5rem /*200px*/;
}

